@import "../config/colors";
@import "../config/breakpoints";

.nav {
  background: $color-black;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 120px;
  @include media($breakpoint-md) {
    height: 100px;
    padding: 60px; }
  @include media($breakpoint-sm) {
    height: 80px;
    padding: 32px; } }

.nav__brand {
  color: white;
  font-size: 32px;
  text-decoration: none; }

.nav__icon {
  height: 45px; }
